const app = getApp()
import serverUrl from '../../common/serveUrl.js'
export default {
	data() {
		return {
			amount:0,
			orderName:'',
			paytype:'alipay',//支付类型
			
			
			userId:'',      //用户id
			addressId:"",	//地址id
			goodsId:'',		//商品id
			openId:'',      //OpenID
			num:'' ,      //数量
			userMsg:'',
			imgUrl:this.$imgUrl
		};
	},
	onLoad(e) {
		this.amount = parseFloat(e.amount).toFixed(2);
		console.log(e)
		this.userId  =e.userId;
		this.addressId  =e.addressId;
		this.goodsId  =e.goodsId;
		this.openId  =e.openid;
		this.num  =e.num;
		this.userMsg = e.userMsg;
		console.log(this.num)
		uni.getStorage({
			key:'paymentOrder',
			success: (e) => {
				if(e.data.length>1){
					this.orderName = '多商品合并支付'
				}else{
					this.orderName = e.data[0].name;
				}
				console.log("....",e)
				uni.removeStorage({
					key:'paymentOrder'
				})
			}
		})
	},
	methods:{
		doDeposit(){
			var url = serverUrl.serverUrl;
			var that = this;
			uni.showLoading({
				title:'支付中...'
			});
			uni.request({
				url:url+ "/app/order/pay?userId="+this.userId+"&addressId="+this.addressId+"&goodsId="+this.goodsId+
					"&openid="+app.globalData.openid+"&num="+this.num+"&userMsg="+this.userMsg,
				
				method:"POST",
				success(res) {
					console.log(res)
					var orderNum = res.data.data.orderNo
					debugger
					wx.requestPayment({
					  timeStamp: res.data.data.timeStamp,
					  nonceStr: res.data.data.nonceStr,
					  package: res.data.data.package,
					  signType: 'MD5',
					  paySign: res.data.data.paySign,
					  success: (res) => {
						  console.log(res)
						   uni.showToast({
							title:'支付成功'
						   });
						   un.request({
							   uel: url + '/app/order/updateState',
							   data: {
								   orderNum: orderNum
							   },
							   methods: 'POST',
							   success: res => {
								   
							   }
						   })
					  },
					  fail(res) {
					   console.log('支付失败', res)
					  }
					})
				}
			})
		}
	}
}
